import React, { ReactNode, useCallback, useState } from 'react';
import { analyticsService } from '@wk/acm-analytics';
import { Iconsvg } from '@wk/components-react16';

import styles from './accordion-item.scss';

export interface IAccordionItemProps {
    title: string;
    children: ReactNode;
}

const AccordionItemComponent: React.FC<IAccordionItemProps> = ({ title, children }: IAccordionItemProps) => {
    const [isOpen, setIsOpen] = useState(false);

    const accorddionItemClickHandler = useCallback(
        (event: React.MouseEvent) => {
            setIsOpen(!isOpen);
            analyticsService.onSearchElementEvent({
                searchId: null,
                searchQuery: null,
                subType: 'filtersGroup',
                payload: { action: isOpen ? 'collapse' : 'expand' },
            })(event);
        },
        [isOpen]
    );

    return (
        <div className={styles['accordion-item']}>
            <div
                className={styles['accordion-item__header']}
                onClick={accorddionItemClickHandler}
                data-e2e-element-type="accordion"
                data-e2e-element-id="accordionItem">
                <span className={styles['accordion-item__icon']}>
                    {!isOpen ? <Iconsvg name="chevron-right" size={12}></Iconsvg> : null}
                    {isOpen ? <Iconsvg name="chevron-down" size={12}></Iconsvg> : null}
                </span>
                <span className={styles['accordion-item__title']}>{title}</span>
            </div>
            {isOpen ? <div className={styles['accordion-item__body']}>{children}</div> : null}
        </div>
    );
};

export const AccordionItem = React.memo(AccordionItemComponent);
