import React, { useEffect } from 'react';
import { analyticsService } from '@wk/acm-analytics';

import { industryHelpData } from './industry-help-hint-content.data';
import { Accordion } from '../../accordion/accordion';
import { AccordionItem } from '../../accordion/accordion-item/accordion-item';

import styles from './industry-help-hint-content.scss';

const IndustryHelpHintContentComponent: React.FC = () => {
    useEffect(() => {
        analyticsService.onWidgetEvent({
            widgetId: 'industryHelpPopup',
            action: 'open',
            subType: 'popup',
        });
    }, []);

    return (
        <Accordion>
            {industryHelpData.map((group, groupIndex) => (
                <AccordionItem key={group.title + groupIndex} title={group.title}>
                    {
                        <ul className={styles['industry-help-content__list']}>
                            {group.items.map((item, itemIndex) => (
                                <li
                                    key={item + groupIndex + itemIndex}
                                    className={styles['industry-help-content__list-item']}>
                                    {item}
                                </li>
                            ))}
                        </ul>
                    }
                </AccordionItem>
            ))}
        </Accordion>
    );
};

export const IndustryHelpHintContent = React.memo(IndustryHelpHintContentComponent);
